import React, { Component } from "react";
//import logo from "./logo.svg";
import "./App.css";
import Card from './components/Card';
// import CardButton from './components/CardButton';
class App extends Component {
  constructor(props){
    super(props);
    this.state={
    statewise_data:{},
    india_data:{}
    };
  }

  getDataFromAPI=()=>{
    const api_url="https://api.covid19india.org/v4/data.json";
    fetch(api_url,{
      method:'GET',
      mode:'cors',
    }).then(res=>{
      return res.json();
    }).then(data=>{
      console.log(data);
      if(data.TT.total!==undefined)
        {
          this.setState({india_data:data.TT.total});
        }
      let state_data= data;
      delete state_data.TT;
      if(state_data!==undefined)
      {
        this.setState({statewise_data:state_data});
      }
        console.log(this.state.india_data);
        console.log(this.state.statewise_data);

    }).catch(error=>{
      console.log(error);
    })
  }

  componentDidMount(){
    this.getDataFromAPI();
  }
  render() {
    return (
      <div className="App">
        <header>
          <h1>COVID 19 TRACKER - INDIA</h1>
        </header>
        { 
        // <Card />
        }
        <footer>
          <h3>COVID 19 TRACKER PROJECT - SHIJIN RAJ 2021</h3>
        </footer>
      </div>
    );
  }
}

export default App;
