import React, { Component } from 'react'
import '../card.css';
import Counter from '../components/Counter'
class Card extends Component{
constructor(props){
    super(props);
    this.state={
        display:"none",
    }
}
toggleDisplayState=()=>{
    let new_display_state=(this.state.display==="none")?"block":"none";
    this.setState({display:new_display_state});
}
    render(){
        const state_data=this.props.state_data;
       // const recovery_perc = (parseInt(state_data.recovered)/parseInt(state_data.confirmed))*100;
        //const death_perc = (parseInt(state_data.deceased)/parseInt(state_data.confirmed))*100;
        const test_positivity = ((parseInt(state_data.confirmed)/parseInt(state_data.tested))*100).toFixed(2);
        return (
            <div className='card' onClick={this.toggleDisplayState} style={{background:this.state.color}}>
                <img className='bgimage' src="/img/virus.png" alt=""/>
                <h3>{state_data.state_name}</h3>
                <div style={{display:this.state.display}}>

                <Counter counterTitle="Population" counterValue={parseInt(state_data.population).toLocaleString()} progress="100%"/>
                <Counter counterTitle="Tested" counterValue={parseInt(state_data.tested).toLocaleString()} progress="100%"/>
                <Counter counterTitle="Confirmed" counterValue={parseInt(state_data.confirmed).toLocaleString()} progress="100%"/>
                <Counter counterTitle="Positivity Rate" counterValue={test_positivity+"%"} progress="100%"/>
                <Counter counterTitle="Recovered" counterValue={parseInt(state_data.recovered).toLocaleString()} progress="100%"/>
                <Counter counterTitle="Deceased" counterValue={parseInt(state_data.deceased).toLocaleString()} progress="100%"/>
                </div>
            </div>
        );
    }
}

export default Card;